class FreezeButton extends Component {
	render() {
	
	var buttontext
	if(this.props.frozen===false)
	{ buttontext="Freeze" }
	else
	{ buttontext="Unfreeze" }
	
	return (
		<button type="submit" className="button" id="FreezeButton" onClick={ this.props.freeze }>{buttontext}</button>
	)
	
	}
	
}


//Button to open the burger menus (two of them)
class MenuButton extends Component {
	render() {
		
	return (
		<button type="submit" className="button" id={this.props.id} onClick={ this.props.open }>{this.props.children}</button>
		)
  }
}





export default App;






