//var BigNumber = require('bignumber.js');
var Decimal = require('decimal.js');
//import * as Decimal from 'decimal.js';

//BigNumber.config({ DECIMAL_PLACES: 30, ERRORS: false })
Decimal.config({ precision: 30 });


//This JS file does the rest of the number crunching started by App.js,
//using decimal.js to do more precise calculations.
//Decimal.js requires that you multiply and divide, etc. with number.mul(), .div().
//I have left the old, simpler-looking (but equivalent) equations in comments
//so you can seee what's going on.
//I should also note, that CanvasSpace.js does much of this calculation a second time
//in getRelativePoints(). (though it's for a different purpose, and done differently)
//The purpose of crunchnumbers() is mostly just to show the statistics on screen.

var PI = Decimal.acos(-1);

//finds the coin's landing position on the circle, and max height achieved.
//export function crunchnumbers( radius, omega, start_v_x, start_v_y, r_oncoin )
export function crunchnumbers( inputvalues )
{
	
	//Calculate misc. variables /////////////////////////////////////
	/////////////////////////////////////////////////////////////////
	var anglefromVertical=inputvalues.anglefromVertical
	var speed=inputvalues.speed
	var accel_earth=inputvalues.accel_earth
	var percentgravity=inputvalues.percentgravity
	var diameter=inputvalues.diameter
	var startheight=inputvalues.startheight
	
	var computedangle = (-1* anglefromVertical * Math.PI / 180) + (Math.PI / 2);

		//x component of speed without the part from rotation
	var relative_v_x = speed * Math.cos(computedangle)
		//y matches reality
	var relative_v_y = speed * Math.sin(computedangle)
	
	var expectedheight
	var expectedtime
	
	if(relative_v_y > 0)  //was thrown upwards
	{			
		//var expectedheight = height_start + height_thrown; //works out exactly the same for case throw "up"

		expectedheight = (Math.pow(relative_v_y,2) / (2 * accel_earth)) + startheight;
		expectedtime = Math.sqrt( 2 * expectedheight / accel_earth ) + ( relative_v_y / accel_earth)  //same formula as throw "up"
	}
	else if(relative_v_y <= 0)  //was thrown downwards
	{
		expectedheight = startheight;
		
		var end_v_y = -1 * Math.sqrt( Math.pow(relative_v_y,2) + 2*accel_earth*startheight )  //temporary variable
		expectedtime = Math.abs(2 * expectedheight / (relative_v_y + end_v_y))
	}

	var g_accel = accel_earth * percentgravity / 100
	
	var omega = Math.sqrt( g_accel / (diameter/2) )  //radians per second of station
	var standingvelocity = Math.sqrt( g_accel * (diameter/2) )
	
	var r_oncoin = (diameter/2) - startheight	//radius at coin start
	var g_oncoin = omega*omega*r_oncoin;		//gravity acting on the coin
	
	var standingcoin		//standing velocity of coin
	if(startheight < diameter/2)		//if coin started from below the radius (almost always)
	{  standingcoin = -1 * Math.sqrt( g_oncoin * r_oncoin ) } //-1 b/c station is rotating clockwise
	else			//if coin starts above the center of rotation, it's actually going the other way
	{  standingcoin = Math.sqrt( g_oncoin * r_oncoin ) } //-1 b/c station is rotating clockwise
	
	var start_v_x = relative_v_x + standingcoin	//actual starting speed of coin
	
	var expecteddist = Math.abs(relative_v_x * expectedtime) //expected distance on Earth
	var start_speed = Math.sqrt( Math.pow(start_v_x,2) + Math.pow(relative_v_y,2) ) 
		//actual start speed of coin with standing velocity incorporated
	
		//the coin's actual y velocity doesn't change when standing speed is incorporated
	var start_v_y = relative_v_y 
	var radius = diameter/2
	
	//Find the coin's intersection with the circle.///////////////////////
	/////////////////////////////////////////////////////////////////////
	
	//console.log("omega",omega)
	if(Math.abs(start_v_x) < 1e-2)
	{ start_v_x=new Decimal(0) }
	if(Math.abs(start_v_y) < 1e-2)
	{ start_v_y=new Decimal(0) }
	
	radius = new Decimal(radius)
	omega = new Decimal(omega)
	start_v_x = new Decimal(start_v_x)
	start_v_y = new Decimal(start_v_y)
	r_oncoin = new Decimal(r_oncoin)
	
	//console.log("start_v_x", Number(start_v_x))
	
	//slope of ball's path
	//var slope_ball = start_v_y / start_v_x
	var slope = start_v_y.div( start_v_x )  //Decimal.js requires that I
											//divide using .div()
											//and multiply using .mul()
	
	//two possible forms of quadratic equation for both x and y.
	//Solve for x_1, x_2, y_1, y_2. (coin points)
	//Punch in the x answer to absolute value y-equations to match up
	//to the correct y before doing quadratic again for y.
	//sqroot1 and sqroot2 are intermediate calculations.
	//sqroot1 is needed for the x calculation, and sqroot2 is needed for y.	
	
	//var sqroot1 = Math.sqrt( Math.pow(slope_ball,2) * Math.pow(r_oncoin,2) - ((1 + Math.pow(slope_ball,2)) * ( Math.pow(r_oncoin,2) - Math.pow(radius,2)  )) )
	var sqroot1 = ( slope.pow(2).mul( r_oncoin.pow(2)).sub((new Decimal(1).add( slope.pow(2) )).mul( r_oncoin.pow(2).sub( radius.pow(2) )) ) ).sqrt()

	//var sqroot2 = Math.sqrt( Math.pow(r_oncoin,2) - ( 1 + Math.pow(slope,2) ) * ( Math.pow(r_oncoin,2) - Math.pow(radius,2) * Math.pow(slope,2) ) )
	var sqroot2 = ( r_oncoin.pow(2).sub(( new Decimal(1).add( slope.pow(2) ) ).mul( r_oncoin.pow(2).sub( radius.pow(2).mul( slope.pow(2))))) ).sqrt()
	
	
	
	//first nail down the final position of the coin
	var x_1
	var x_2
	var y_1
	var y_2
	var x_f	//final spot of coin x
	var y_f	//final spot of coin y
	
	if(Number(slope)<0 && Number(start_v_x) < 0)
	{
		//var x_2 = ( (slope * r_oncoin) - sqroot1 ) / ( 1 + Math.pow(slope,2)) // -
		//var y_2_abs = Math.sqrt(Math.pow(radius,2) - Math.pow(x_2,2) )
		//var y_2 = ( (-1 * r_oncoin) + sqroot2 ) / ( 1 + Math.pow(slope,2) ) // +
		
		//Apparently the (-) from the Y eqn goes with the (+) in the X eqn, just to be confusing.		
		
		x_2 = ( (slope.mul( r_oncoin)).sub( sqroot1 )).div( new Decimal(1).add( slope.pow(2)) ) // -
		y_2 = ( new Decimal(-1).mul(r_oncoin).add( sqroot2 ) ).div( new Decimal(1).add(slope.pow(2) ) ) // +
		
		x_f = x_2
		y_f = y_2
	}
	else if(slope > 0 && Number(start_v_x) > 0)
	{
		//var x_1 = ( (slope * r_oncoin) + sqroot1 ) / ( 1 + Math.pow(slope,2)) // +
		//var x_1 = ((slope.mul(r_oncoin)).add(sqroot1)).div( new Decimal(1).add(new Decimal.pow(slope,2)))
		
		x_1 = ((slope.mul(r_oncoin)).add(sqroot1)).div( new Decimal(1).add(slope.pow(2))) //+
		y_2 = ( new Decimal(-1).mul(r_oncoin).add( sqroot2 ) ).div( new Decimal(1).add( slope.pow(2) ) ) // +		
		
		x_f = x_1
		y_f = y_2
	}
	else if(slope < 0 && Number(start_v_y) < 0)
	{
		x_1 = ((slope.mul(r_oncoin)).add(sqroot1)).div( new Decimal(1).add(slope.pow(2))) //+
		y_1 = ((new Decimal(-1).mul(r_oncoin)).sub( sqroot2 )).div( new Decimal(1).add( slope.pow(2)) ) // -
		
		x_f = x_1
		y_f = y_1
	}
	else if((slope > 0 && Number(start_v_x) < 0) || slope == 0 )
	{
		//var y_1_abs = Math.sqrt(Math.pow(radius,2) - Math.pow(x_1,2) ) 
		//var y_1 = ( (-1 * r_oncoin) - sqroot2 ) / ( 1 + Math.pow(slope,2) ) // -
		
		x_2 = ( (slope.mul( r_oncoin)).sub( sqroot1 )).div( new Decimal(1).add( slope.pow(2)) ) // -
		y_1 = ((new Decimal(-1).mul(r_oncoin)).sub( sqroot2 )).div( new Decimal(1).add( slope.pow(2)) ) // -
				
		x_f = x_2
		y_f = y_1
	}
	
	var time
	var maxvalue
	if(slope.isFinite() && Number(start_v_x) != 0 )
	{
		//total (absolute) starting velocity
		//var start_vtotal = Math.sqrt( Math.pow(start_v_x,2) + Math.pow(start_v_y,2) )
		var start_vtotal = ( start_v_x.pow(2).add(start_v_y.pow(2) ) ).sqrt()
		
		//Get the time in the air, now that we know the landing spot.
		//var time = Math.sqrt( Math.pow(x_f,2) + Math.pow( ( y_f + r_oncoin ),2 ) ) / start_vtotal
		time = ( x_f.pow(2).add(( y_f.add(r_oncoin)).pow(2) )).sqrt().div( start_vtotal )
				
		
		///MAX HEIGHT/////////////////////////////////////
		//////////////////////////////////////////////////

		//a maximization function.
		//This would have made more sense if I did it in reference to time instead.
		//Oh well. This works.
		if(Number(start_v_y) > 0)
		{
			//Starting values
			var test_x = start_vtotal*(time/10) //Starting test increment
			var direction = -1  //iterate leftwards to start.
			maxvalue = radius.sub(r_oncoin)  //start with initial height
			var current_x = 0    //ball goes left. Negative.
			var current_y = 0
			var current_value = 0
			var disttocenter = 0
			var lastvalue = 0
			var timesinarow = 0
			
			for(var i=0; i<1000; i++)
			{
				current_x = current_x + (test_x * direction)
				
				//y = mx + b
				current_y = (slope * current_x) - r_oncoin
				
				//pythagoras, to find distance to ball from center of station
				disttocenter = Math.sqrt( Math.pow(current_x,2) +Math.pow(current_y,2) )
				
				current_value = radius - disttocenter
				
				if(current_value<lastvalue)
				{  //if the values are decreasing
					//then it's the wrong direction.
					direction = direction * -1;
					test_x = test_x / 2;
				}

				if(current_value > maxvalue)
				{ maxvalue = current_value  }
				
				if( Math.abs(current_value - lastvalue) < .0001 )
				{ timesinarow += 1	}
				else
				{ timesinarow = 0 }
				
				// eslint-disable-next-line eqeqeq
				if(timesinarow == 5)
				{
					break;
				}
				
				lastvalue = current_value;
			}
		}
		else //if it was thrown downwards
		{
			maxvalue = radius - r_oncoin;
		}
	}
	else if( !slope.isFinite() && Number(start_v_y) > 0 )
	{
		time = (((new Decimal(2).mul(radius)).sub( radius.sub(r_oncoin) ) ).div(start_v_y)).abs()
		x_f = new Decimal(0)
		y_f = radius
		maxvalue = radius
	}
	else if( !slope.isFinite() && Number(start_v_y) < 0 )
	{
		time = ( radius.sub(r_oncoin).mul(-1) ).div(start_v_y)
		x_f = new Decimal(0)
		y_f = new Decimal(0)
		maxvalue = radius.sub(r_oncoin)
	}
	
	
	
	
	
	///Distance and curvilinear distance at landing//////////////
	////////////////////////////////////////////////////////////

	//if start_v_x==0 AND start_v_y == 0, then the coin is frozen in the air and the answers remain undefined.
	if(isFinite(time))
	{
		//radial angle swept by the person
		
		//var theta_traversed_person = omega * time
		//var initial_angle_person = (3 * Math.PI) / 2
		//var final_angle_person = initial_angle_person - theta_traversed_person //minus b/c clockwise rotation
		var theta_traversed_person = Number( omega.mul( time ) )
		var initial_angle_person = (new Decimal(3).mul( PI )).div(2)
		var final_angle_person = initial_angle_person.sub(theta_traversed_person)
		
		
		//final position of person
		//fortunately Javascript uses radians, not degrees
		
		//var xf_person = radius * Math.cos(final_angle_person)
		//var yf_person = radius * Math.sin(final_angle_person)
		var xf_person = radius.mul( final_angle_person.cos() )
		var yf_person = radius.mul( final_angle_person.sin() )
		
		
		//var x_difference = xf_person - x_f
		//var y_difference = yf_person - y_f	
		//var total_difference = Math.sqrt( Math.pow(x_difference,2) + Math.pow(y_difference,2) )
		var x_difference = xf_person.sub( x_f )
		var y_difference = yf_person.sub( y_f )
		var total_difference = ( x_difference.pow(2).add(y_difference.pow(2) ) ).sqrt()	//your final answer!

		
		//Now also calculate the coin's curvilinear distance difference (along floor)
		
		var coin_distance = Math.sqrt(Math.pow(x_f,2) + Math.pow( Number(radius) + Number(y_f),2))
			//distance from floor where person started (270deg) to where coin lands
		

		//(formula) kahn acadamy says chordlength = 2radius * sin(angle/2)
		//so using that you can get the angle of the chord.
		var half_chord_value = coin_distance/(2*radius)
			//a bugfix for arcsin, if the coin lands almost exactly halfway across
			//then glitchy float values could give you 1.000000000001 and crash.
		if(half_chord_value > 1) { half_chord_value = 1 }  
		var theta_traversed_coin = 2 * Math.asin(half_chord_value) 
				//goes from 3PI/2 start to where the coin lands.
		
		var new_theta_traversed_person = theta_traversed_person
		while(new_theta_traversed_person > 2*Math.PI)
		{ new_theta_traversed_person -= 2* Math.PI}
		
		//var directionleft = true  //coin lands to the left of the person
		
		//correct the arcsin answer if the coin lands in quadrant 1 or 4
		if(x_f>0) { theta_traversed_coin += 2*(Math.PI-theta_traversed_coin) }
		
		var angular_diff = Math.abs(new_theta_traversed_person-theta_traversed_coin)
		if(angular_diff > Math.PI) {  angular_diff = (2* Math.PI) - angular_diff }
		var curvilinear = angular_diff * radius  //convert from radians to feet
				//distance *along floor* at landing
		
	}

	if(!isFinite(time))
	{ maxvalue = Math.abs(radius - r_oncoin) }
	
	//console.log("Curvilinear", Number(curvilinear))
	
	return {
		maxheight:Number(maxvalue),
		//x_f : Number(x_f),
		//y_f : Number(y_f),
		time: Number(time),
		slope: Number(slope),
		total_difference: Number(total_difference),
		curvilinear: Number(curvilinear),
		//directionleft: directionleft, //thus negative = left of person.
		theta_traversed_person: theta_traversed_person,  //has already been converted to a number.
		theta_traversed_coin: theta_traversed_coin,
		
		expectedtime: expectedtime,
		expectedheight: expectedheight,
		standingvelocity: standingvelocity,
		omega: omega,
		start_v_x: start_v_x,
		standingcoin: standingcoin,
		g_oncoin: g_oncoin,			//only in output menu
		expecteddist: expecteddist,
		start_speed: start_speed,	//only in output menu
		
		
	}
}










//Removed from above

/*
		//used to know if the coin landed to the left (for the output).
		//This may be excessive. Calculates curvilinear distance.

		//The logic is as follows: if the coin was thrown *to the right*
		//but then landed to the left of the person (viewer's left, not thrower's)
		//then the difference from expected should be the expected landing spot
		//PLUS the actual distance away.

		//Otherwise, if it was thrown AND lands in the same direction, then
		//it should be the DIFFERENCE between the two.


		//this part seems way more complicated than it needs to be
		if(x_f > 0 && xf_person < 0 && y_f < 0 && yf_person < 0) //Person in q3 and coin in q4
			{ directionleft = false }
		else if( x_f > 0 && xf_person > 0)      //Both are anywhere in the right two quadrants (1 & 4)
			{ theta_traversed_coin += Math.PI //correct the arcsin step above
				if(theta_traversed_coin < new_theta_traversed_person) 
					{ directionleft = false }	 }
		else if(x_f < 0 && y_f > 0 && xf_person > 0 && yf_person > 0 )  //coin in q2 and person in q1
			{ directionleft = false }
		else if(x_f < 0 && xf_person < 0)  //if they're both in Q2 or Q3
			{ 	if(theta_traversed_coin < new_theta_traversed_person) 
					{ directionleft = false; } }
		else if( x_f > 0 && yf_person < 0) //Coin in Q1or4 person in Q3 
			{								//(person in Q4 case is above)
				//do nothing
			}
		else if( x_f > 0 ) //Coin in Q1or3 person in Q2 (other quadrants met above)
			{	theta_traversed_coin += Math.PI; //console.log("hello")
				if( theta_traversed_coin - new_theta_traversed_person > Math.PI ) //Q1&3 or Q4&2 (person in 1 or 4)
					{ directionleft = false  } }
		else if( (x_f < 0 && y_f > 0 && xf_person > 0 && yf_person < 0 ) ||		//coin in q2 and person in q4
				 (x_f < 0 && y_f < 0 && xf_person > 0 && yf_person > 0 ) ) 		//coin in q3 and person in q1
			{ 	if(new_theta_traversed_person - theta_traversed_coin < Math.PI )
					{ directionleft = false  } }
		
		//if any of these conditions are not met, then directionleft remains true.
		*/



